.Hangman-word {
    letter-spacing: 0.4em;
    font-size: 2rem;
    color: #333;
    word-wrap: break-word;
}  

.Hangman button {
    border: none;
    font-family: inherit;
    font-size: 2rem;
    cursor: pointer;
    width: 3rem;
    margin: 1px 1px 1px 1px;
    background: #ccc;
    color: #000;
    border-radius: 10px;
}

.Hangman-reset {
    color: #ddd;
    width: 10rem !important;
}


.Hangman button:hover {
    background-color: rgb(160, 255, 160);
}

.Hangman button:disabled {
    background-color: #a00000;
}

.text-center{
    text-align: center;
    color: #333;
}

.text-center h1{
    font-size: 60px;
}

.text-center h2{
    font-size: 30px;
}

.top-part {
    padding: 5px;
    margin: 5px;
}

.bottom-part {
    margin: 5px;
}